import React from "react";
import "./Contact.css";
function Contact() {
  return (
    <div className="contact">
      <div>
        <h1>Hire Me For Your Next Dream Project</h1>
        <form name="contact" netlify>
          <div>
            <label for="name">Name</label>
            <input
              type="text"
              name="name"
              placeholder="Harry Potter"
              required
            ></input>
          </div>
          <div>
            <label for="email">Email</label>
            <input
              type="email"
              name="email"
              placeholder="abc@abc.com"
              required
            ></input>
          </div>
          <div>
            <label for="email">Phone No</label>
            <input
              type="text"
              name="phoneno"
              placeholder="0123456789"
              required
            ></input>
          </div>
          <div>
            <label for="name">Message</label>
            <textarea type="text" placeholder="My message" required></textarea>
          </div>
          <div id="center">
            <button type="submit" className="button">
              Submit
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Contact;
