import React from "react";
import Card from "./Card";

import "./Services.css";

import checklist from "../../assets/checklist.png";
import wordpress from "../../assets/wordpress.png";
import design from "../../assets/design.png";
import web from "../../assets/web.png";
import webdesign from "../../assets/web-design.png";
import test from "../../assets/test.png";

function Services() {
  const services = [
    {
      id: 1,
      services: "WEB DEVELOPMENT",
      p1: "Responsive and Fast",
      logo: web,
      description:
        " Every website should be built with two primary goals: Firstly, it needs to work across all devices. Secondly, it needs to be fast as possible.",
    },
    {
      id: 12,
      services: "Web Design",
      p1: "User Focused",
      logo: design,
      description:
        "A website should be designed for the people who will use it, so that's exactly what I do. User focused design should be the primary goal of any website.",
    },
    {
      id: 123,
      services: "WordPress DEVELOPMENT",
      p1: "Responsive and Fast",
      logo: wordpress,
      description:
        " Every website should be built with two primary goals: Firstly, it needs to work across all devices. Secondly, it needs to be fast as possible.",
    },
    {
      id: 1234,
      services: "Web Audits",
      p1: "Improving the Performance",
      logo: checklist,
      description:
        " Every website should be built with two primary goals: Firstly, it needs to work across all devices. Secondly, it needs to be fast as possible.",
    },
    {
      id: 12345,
      services: "Logos and Graphic Designing",
      p1: "UI & UX",
      logo: webdesign,
      description:
        " Every website should be built with two primary goals: Firstly, it needs to work across all devices. Secondly, it needs to be fast as possible.",
    },

    {
      id: 123456,
      services: "Quality Assurances for All Platforms",
      p1: "Manual Testing",
      logo: test,
      description:
        "Handle QA Responsibilities for back-end and front-end of the different platforms like APK, web, IOS, etc. Mainly Apple Tv, Web, Android Web, Android Apps.",
    },
  ];
  return (
    <div>
      <div className="service">
        <h1>Services</h1>
        <div className="flexi2">
          {services.map((e) => (
            <Card
              services={e.services}
              p1={e.p1}
              logo={e.logo}
              description={e.description}
              key={e.id}
            />
          ))}
        </div>
        <div className="center">
          <button className="button">Learn More</button>
        </div>
      </div>
    </div>
  );
}

export default Services;
