import React from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";

function Navbar() {
  const NavSlide = (e) => {
    const burger = document.querySelector(".open");
    const nav = document.querySelector(".menu");

    //Toggle Nav

    nav.classList.toggle("nav-active");

    //burger animation
    burger.classList.toggle("toggle");
  };

  return (
    <nav className="navbar">
      <header>
        <h2>WebEngines</h2>
      </header>
      <ul className="menu">
        <Link to="/">
          <li className="links">Home</li>
        </Link>
        <Link to="/about">
          <li className="links">About Us</li>
        </Link>
        <Link to="/services">
          <li className="links">Services</li>
        </Link>
        <Link to="/skills">
          <li className="links">Skills</li>
        </Link>
        <Link to="/projects">
          <li className="links">Projects</li>
        </Link>
        <Link to="/contact">
          <li className="links">Contact Us</li>
        </Link>
        <Link to="/blogs">
          <li className="links">Blogs</li>
        </Link>
      </ul>
      <div className="menu-desk">
        <ul className="desk">
          <Link to="/">
            <li className="links">Home</li>
          </Link>
          <Link to="/about">
            <li className="links">About Us</li>
          </Link>
          <Link to="/services">
            <li className="links">Services</li>
          </Link>
          <Link to="/skills">
            <li className="links">Skills</li>
          </Link>
          <Link to="/projects">
            <li className="links">Projects</li>
          </Link>
          <Link to="/contact">
            <li className="links">Contact Us</li>
          </Link>
          <Link to="/blogs">
            <li className="links">Blogs</li>
          </Link>
        </ul>
      </div>
      <div className="open" onClick={NavSlide}>
        <div className="line1"></div>
        <div className="line2"></div>
        <div className="line3"></div>
      </div>
    </nav>
  );
}

export default Navbar;
