import React from "react";
import "./Projects.css";
function Cards({ style, logo, link, description, project }) {
  return (
    <article className={style}>
      <span className="image">
        <img src={logo} alt="" />
      </span>
      <a href={link}>
        <h2>{project}</h2>
        <div className="content">
          <p>{description}</p>
        </div>
      </a>
    </article>
  );
}

export default Cards;
