import React from "react";
import Cards from "./Cards";

import "./Skill.css";

function Skill() {
  const Skills = [
    { skill: "HTML5", exp: "1.5 Y", logo: "fab fa-html5", rate: "4+" },
    { skill: "CSS3", exp: "1.5 Y", logo: "fab fa-css3-alt", rate: "3" },
    { skill: "JAVASCRIPT", exp: "1.5 Y", logo: "fab fa-js-square", rate: "3" },
    { skill: "JQUERY", exp: "1.5 Y", logo: "fab fa-js-square", rate: "4+" },
    { skill: "WORDPRESS", exp: "1.5 Y", logo: "fab fa-wordpress", rate: "4+" },
    { skill: "REACT JS", exp: "BEG", logo: "fab fa-react", rate: "2.5" },
    { skill: "BOOTSTRAP", exp: "1.5 Y", logo: "fab fa-bootstrap", rate: "2.5" },
  ];

  const Backend = [
    { skill: "PHP", exp: "1.5 Y", logo: "fab fa-php", rate: "3.5" },
    { skill: "MYSQL", exp: "1.5 Y", logo: "fas fa-database", rate: "2+" },
    { skill: "NodeJS", exp: "BEG", logo: "fab fa-node", rate: "4+" },
    { skill: "MongoDB", exp: "BEG Y", logo: "fas fa-server", rate: "2+" },
  ];

  const QA = [
    {
      skill: "Quality Assurances for Android Apps",
      exp: "5 M",
      logo: "fab fa-android",
      rate: "3.5",
    },
    {
      skill: "Quality Assurances for IOS Apps",
      exp: "5 M",
      logo: "fab fa-apple",
      rate: "3.5",
    },
    {
      skill: "Quality Assurances for WEB and mWeb",
      exp: "5 M",
      logo: "far fa-window-restore",
      rate: "3.5",
    },
    {
      skill: "Quality Assurances for TV Apps",
      exp: "5 M",
      logo: "fas fa-tv",
      rate: "3.5",
    },
  ];

  const Graphic = [
    {
      skill: "Logo Designing",
      exp: "BEG",
      logo: "far fa-images",
      rate: "3.5",
    },
    {
      skill: "Adobe Illustrator",
      exp: "BEG",
      logo: "fas fa-ruler-combined",
      rate: "4+",
    },
    {
      skill: "Adobe Photoshop",
      exp: "BEG",
      logo: "fas fa-photo-video",
      rate: "2+",
    },
    {
      skill: "UI & UX Design",
      exp: "BEG",
      logo: "fas fa-pencil-ruler",
      rate: "2+",
    },
  ];
  return (
    <div className="banner">
      <h1>SKILLS</h1>
      <div className="banner">
        <h1>Front-End</h1>
        <div className="flexi">
          {Skills.map((e) => (
            <Cards
              skill={e.skill}
              exp={e.exp}
              logo={e.logo}
              rate={e.rate}
              key={Math.floor(Math.random() * 5000)}
            />
          ))}
        </div>
      </div>
      <div className="banner">
        <h1>Back-End</h1>
        <div className="flexi">
          {Backend.map((e) => (
            <Cards
              skill={e.skill}
              exp={e.exp}
              logo={e.logo}
              rate={e.rate}
              key={Math.floor(Math.random() * 5000)}
            />
          ))}
        </div>
      </div>

      <div className="banner">
        <h1>QA Engineer</h1>
        <div className="flexi">
          {QA.map((e) => (
            <Cards
              skill={e.skill}
              exp={e.exp}
              logo={e.logo}
              rate={e.rate}
              key={Math.floor(Math.random() * 5000)}
            />
          ))}
        </div>
      </div>
      <div className="banner">
        <h1>UI AND UX DESIGN</h1>
        <div className="flexi">
          {Graphic.map((e) => (
            <Cards
              skill={e.skill}
              exp={e.exp}
              logo={e.logo}
              rate={e.rate}
              key={Math.floor(Math.random() * 5000)}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

export default Skill;
