import React from "react";

function Jobs({ job, logo, l1, l2, l3, l4, l5, joining, Company }) {
  return (
    <div className="jobs">
      <div className="profile">
        <img src={logo} alt="xargs" />
      </div>
      <div>
        <h1>{job}</h1>
        <h2>{Company}</h2>
        <span>{joining}</span>
        <ul>
          <li>{l1}</li>
          <li>{l2}</li>
          <li>{l3}</li>
          <li>{l4}</li>
          <li>{l5}</li>
        </ul>
      </div>
    </div>
  );
}

export default Jobs;
