import React from "react";

function Card({ services, p1, logo, description }) {
  return (
    <div className="service-card">
      <div className="servicecard-header">
        <img alt="services" src={logo} />
      </div>
      <div className="servicecard-body">
        <h1>{services}</h1>
        <p className="p1">{p1}</p>
        <p className="p2">{description}</p>
      </div>
    </div>
  );
}

export default Card;
