import React from "react";

import "./Skill.css";
function Cards({ skill, exp, logo, rate }) {
  return (
    <div className="cards">
      <div className="cards-head">
        <i className={logo}></i>
      </div>
      <div className="cards-body">
        <h1>{skill}</h1>
        <div className="exps">
          <span>Exp:- {exp}</span>
          <span>Level: {rate}</span>
        </div>
      </div>
    </div>
  );
}

export default Cards;
